.projectparent{
    /* border: 1px solid red; */
    padding: 30px;
    background-color: rgba(255, 0, 0, 0.289);
}

.headingproject{
    font-size: 50px;
    font-weight: 100;
}
.pcard{
    display: flex;
    border: 3px solid black;
    width: 65%;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    transition-duration: 1s;
}
.pcard:hover{
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}
.pcard > div {
    width: 50%;
}
.pcard > div:nth-child(2)
{
    background-color:darkorange;
    color: white;
}

.pcard > div:nth-child(2) button{
    background-color: black;
    margin: 20px;
}

.banner>img{
  width: 100%;
  height: 100%;
}


@media (max-width:730px){
    
    .projectparent{
        width: 730px;
        max-width: 730px;
    }

    .pcard{
        flex-direction: column;
        width: 74%;
        margin-left: 10%;
        margin-right: 16%;
        
    }
    .pcard> div{
        width: 100%;
    }
}