.introparent{
    /* border: 1px solid black; */
    /* width: 100%; */
     
    height: 90vh;
    padding-top: 40px;
    background-color: rgba(0, 255, 255, 0.376);
}
.firstline{
    line-height: 30px;
    font-size: 20pxnode;
}

.introname{
    /* border: 1px solid red; */
    width: 60%;
    height: 50%;
    margin: auto;
    text-align: left;
    line-height: 10px;
    display: flex;
}

.introname > div:nth-child(1){
    width: 75%;
    /* border: 1px solid red; */
}
.introname>div:nth-child(2){
    width: 25%;
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;

}
.introname > div:nth-child(1) > button{
     display: flex;
     justify-content: center;
     align-items: center;
    height: 35px;
    width: 100px;
    background-color:darkorange;
    color: black;

}

.introname > div:nth-child(1) > button:hover{

    
    background-color:darksalmon;
    color: black;

}

.introname>div:nth-child(2) > img{

    width: 170px;
    height: 230px;
    border-radius: 20px;
    border: 1px solid black;

}

.introtwo {
    /* border: 1px solid green; */
    width:70%;

    margin:auto;
}

.introtwo > p:nth-child(1)
{
    font-size: 35px;
}
.introtwo > p:nth-child(2)
{
    text-align: justify;
}

.candidatename{
    font-size: 50px;
}

 @media (min-width:620px) and (max-width:1000px){
 .candidatename{
    font-size: 35px;
 }

 
 .introname{
    line-height: 20px;
    
 }

 .introname>div:nth-child(2) > img {

width: 120px;
max-width: 120px;
height: 190px;
margin-left: 60px;

 }

} 

@media (max-width:720px)
{


    .introparent{
        width: 720px;
        max-width: 720px;
        height: auto;
        padding-bottom: 40px;
    }

    .candidatename{

        font-size: 20px;
        
    }

    .introname>div:nth-child(2) > img {

        width: 120px;
        height: 190px;
        margin-left: 60px;
        
         }

    
} 